*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html{
    font-size: 10px;
}
body{
    min-height: 100vh;
    font-size: 1.6rem;
    background-color: hsl(185, 75%, 39%);
    font-family: 'Kumbh Sans', sans-serif;
}
.boss{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    // background: url(images/bg-pattern-top.svg) no-repeat fixed  left top;
    min-height: 100vh;
    width: 100%;
    position: relative;
    overflow: hidden;

    &:before{
        content: url(images/bg-pattern-top.svg);
        position: absolute;
        right: 50%;
        bottom: 40%;
        z-index: -1;
    }

    &:after{
        content: url(images/bg-pattern-bottom.svg);
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: -1;
    }
}

.container{
     
    display: flex;
    flex-flow: column wrap;
    // padding: 1.6rem 2.6rem;
   
    

    .top{
        border-radius: 1.6rem 1.6rem 0 0 hsl(229, 23%, 23%, .2) ;
        position: relative;
        
     &:after{
         content: url(images/image-victor.jpg);
         position: absolute;
         bottom: 0%;
         left: 50%;
         width: 9.5rem;
         height: 9.5rem;
         transform: translate(-50%, 50%);
         border-radius: 50%;
         overflow: hidden;
        //  background: url(images/image-victor.jpg) fixed center no-repeat;
         background-size: cover;
         border: .5rem solid #fff;    
         vertical-align: center;  
        //  display: inline-block;   

        img{
            display: block;
            // mix-blend-mode: multiply;
            width: 100%;
            height: 100%;
            // vertical-align: center;
        }
     }

        img{
            border-radius: 1.6rem 1.6rem 0 0;
        }
    }

    .bottom{
        display: flex;
        flex-flow: column;
        align-items: center;
        background-color: #fff;
        height: 25rem;
        margin-top: -4px;
        border-radius: 0 0 1.6rem 1.6rem;
      
        // padding-top: 2rem;
         h4{   
               font-size: 1.8rem;
               font-weight: 700;
               color: hsl(229, 23%, 23%)    ;

               span{
                   font-weight: 400;
                   color:hsl(0, 0%, 59%);  
                   margin-left: .5rem; 
               }
           }
          p{
               font-weight: 400;
               font-size: 1.5rem;
               color: hsl(0, 0%, 59%);
               margin-top: .5rem;
           }
        .name{
            display: flex;
            flex-flow: column;
            justify-content: flex-end;
            align-items: center;
            width: 80%;
            height: 65%;
           text-align: center;
           padding-bottom: 3rem;
          
        }
        .likes{
            width: 80%;
            display: flex;
            justify-content: space-between;
            min-height: 35%;
            padding: 2rem;
            text-align: center;
            border-top: 1px solid hsl(185, 75%, 39%, .2);
            position: relative;
            

            &:after{
                position: absolute;
                content: "";
                background:  hsl(229, 23%, 23%, .2);
                filter: blur(1rem);
               top: 100%;
                left: 0;
                width: 100%;
                height: 2.5rem;
            }   

        }
    }
}